<template>
    <div> Loading... </div>
</template>
<script>
import roles from '../router/roles';
    export default {
        name: 'app',
        components: {
        },
        methods: {
        },
        computed: {

        },
        async created() {
        var token = process.env.NODE_ENV ==="development"? this.$root.user.access_token: this.$store.state.clientAppToken;
        await this.$store.commit("updateSession",token);
        switch(this.$store.state.clientAppDecodedToken.Role)
        {
            case roles.MfAdmin:
            case roles.MfAccountManager:
            case roles.MfWarehouseIncharge:
            case roles.TsspAdmin:
            case roles.TsspIntermediate:
            case roles.TaxAuthAdmin:
            case roles.TaxAuthRevenueOfficer:
                this.$router.push("MfOrders");
                break;
            case roles.PrintPartner:
                this.$router.push("PrintOrders");
                break;
             case roles.TpsafAdmin:
             case roles.TpsafFacilityIncharge:
             case roles.TpsafFacilityAdmin:
                 this.$router.push("InternalStockRequest")
                 break;
        }
           
        }
    }
</script>